<template>
  <section
    class="info-notifications"
    v-click-outside="handleClickOutside"
  >
    <div
      class="info-notifications__icon"
      @click="showNotifications = !showNotifications"
    >
      <Icon name="info-circle" />
    </div>
    <Container
      v-if="showNotifications"
      box-shadow
      background-color="white"
      class="info-notifications__body"
    >
      <InfoNotificationsItem
        v-for="notification in notifications"
        :key="notification.id"
        :description="notification.description"
        class="info-notifications__body-item"
      />
    </Container>
  </section>
</template>

<script>
import { ref } from 'vue'
import Icon from '@/components/Icon/Icon'
import clickOutside from '@/directives/clickOutside'
import InfoNotificationsItem from '@/components/Tools/InfoNotifications/InfoNotificationsItem'
import Container from '@/components/Container'

export default {
  name: 'InfoNotifications',
  components: {
    Container,
    InfoNotificationsItem,
    Icon
  },
  directives: {
    clickOutside
  },
  setup () {
    const showNotifications = ref(false)
    const notifications = ref([
      {
        id: 1,
        description: 'Deposits are processed after 2 confirmations on the blockchain.'
      },
      {
        id: 2,
        description: 'Underpaid/Overpaid deposits will be swapped proportionately'
      }
    ])

    const handleClickOutside = () => {
      showNotifications.value = false
    }
    return {
      showNotifications,
      notifications,
      handleClickOutside
    }
  }
}
</script>

