.info-notifications {
  cursor: pointer;
  position: relative;

  &__icon {
    position: relative;
    padding: 11px;
    border-radius: 4px;

    &:hover {
      background-color: map-get($colors, "default");
    }
  }

  &__body {
    position: absolute;
    top: calc(100% + 14px);
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    padding: 20px;
    width: 360px;

    &-item:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

